body {
  text-align: center;
  background-color: black;
}

#board {
  margin: 0 auto;
  width: fit-content;
}

p,
input {
  font-family: "Calibri", sans-serif;
  text-align: center;
  width: 62px;
  height: 62px;
  font-size: 2rem;
  font-weight: bold;
  background-color: black;
  border: 1px solid gray;
  color: white;
  margin: 2px;
  display: inline-block;
  vertical-align: top;
  padding: 1px 2px;
  line-height: 2;
}

button {
  background-color: black;
  border: 1px solid gray;
  color: white;
  padding: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 2px;
}

button:hover {
  background-color: gray;
}
